/* src/components/Work.css */

.work-container {
    display: flex;
    flex-wrap: wrap;
    gap: 40px 8px;
    margin-top: 150px;
    padding: 20px;
    font-weight: bold;
    margin-bottom: 200px;
}

.work-item {
    flex: 1 1 1;
    /* max-width: 200px; */
    height: 200px;
    box-sizing: border-box;
}

.work-item img {
    width: auto;
    height: 100%;
}

.work-info {
    margin-top: 2px;
    font-size: 14px;
    color: #333;
    font-size: 12px;
}

@media (max-width: 600px) {
    .work-item {
        /* flex: 1 1 calc(20% - 40px); */
        width: 33vw;
    }
    .work-container {
        
        display: flex;
        flex-wrap: wrap;
        gap: 1px;
        margin-top: 200px;
        padding: 0px;
        font-weight: bold;
    }
}