/* src/components/Carousel.css */

.carousel-container {
    position: relative;
    width: 100%;
    height: 100vh;
    overflow: hidden;
    cursor: default;
}

.carousel-slide {
    display: none;
    justify-content: center;
    align-items: center;
    height: 100%;
    padding: 10px;
}

.carousel-slide.active {
    display: flex;
}

.carousel-slide img {
    max-height: 70vh;
    width: 100%;
    object-fit: contain;
    user-select: none;
}



/* New styles for the overlay divs */
.overlay {
    position: absolute;
    top: 100px;
    bottom: 0;
    width: 50%;
    cursor: pointer;
}

.left-overlay {
    left: 0;
}

.right-overlay {
    right: 0;
}
